import cmdTransition from "@/components/cmd-transition/cmd-transition.vue"
import cmdInput from "@/components/cmd-input/cmd-input.vue"

export default {
  components: {
    cmdTransition,
    cmdInput
  },

  data() {
    return {
      mobile: {
        phone: '',
        code: '',
        passwordOne: '',
        passwordTwo: ''
      },
      passwordReg: /^\w+$/,
      phoneReg: /^[1](([3][0-9])|([4][5-9])|([5][0-3,5-9])|([6][5,6])|([7][0-8])|([8][0-9])|([9][1,8,9]))[0-9]{8}$/,
      safety: {
        time: 60,
        state: false,
        interval: ''
      },
      modifyMobile: false
    };
  },

  watch: {
    /**
     * 监听表单数值
     */
    mobile: {
      handler(newValue) {
        if (this.phoneReg.test(newValue.phone) && newValue.code.length == 6 && newValue.passwordOne.length >= 8 &&
          newValue.passwordOne == newValue.passwordTwo) {
          this.modifyMobile = true;
        } else {
          this.modifyMobile = false;
        }
      },
      deep: true
    }
  },

  methods: {
    /**
     * 提交按钮点击执行
     */
    fnModify() {
      console.log(JSON.stringify(this.mobile));
    },
    /**
     * 获取验证码
     */
    fnGetPhoneCode() {
      if (this.phoneReg.test(this.mobile.phone)) {
        uni.showToast({
          title: "正在发送验证码",
          icon: "loading",
          success: () => {
            // 成功后显示倒计时60s后可在点击
            this.safety.state = true;
            // 倒计时
            this.safety.interval = setInterval(() => {
              if (this.safety.time-- <= 0) {
                this.safety.time = 60;
                this.safety.state = false;
                clearInterval(this.safety.interval);
              }
            }, 1000);
            uni.showToast({
              title: "发送成功",
              icon: "success"
            })
          }
        })
      } else {
        uni.showToast({
          title: "手机号不正确",
          icon: "none"
        })
      }
    }
  },

  beforeDestroy() {
    /**
     * 关闭页面清除轮询器
     */
    clearInterval(this.safety.interval);
  }
}
